<template>
  <v-app id="inspire">
    <v-content>
      <v-container
        class="fill-height"
        fluid
      >
        <v-row
          align="center"
          justify="center"
        >
          <v-col
            cols="12"
            sm="8"
            md="4"
          >
            <v-card class="elevation-12">
              <v-toolbar
                color="primary"
                dark
                flat
              >
                <v-toolbar-title>Please login</v-toolbar-title>
              </v-toolbar>
              <v-form @submit="send_data" v-on:submit.prevent>
                <v-card-text>
                  <v-text-field
                    v-model="login"
                    label="Login"
                    prepend-icon="mdi-account"
                    type="text"
                    autofocus
                  ></v-text-field>

                  <v-text-field
                    v-model="password"
                    label="Password"
                    prepend-icon="mdi-lock"
                    type="password"
                  ></v-text-field>
                  <v-switch 
                    v-model="remember"
                    label="Remember this device for 30 days"
                  ></v-switch>
                  <v-alert
                      v-model="wrong_data"
                      type="error"
                      dismissible
                    >
                    {{ wrong_text }} <v-icon>mdi-smiley-sad-outline</v-icon>
                    </v-alert>                  
                </v-card-text>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="primary" type="submit">Login</v-btn>
                </v-card-actions>
              </v-form>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
//import axios from 'axios'
export default {
  data () {
    return {
      login: localStorage.getItem('login') || '',
      password: '',
      remember: true,
      wrong_data: false,
      wrong_text: ''
    }
  },
  methods: {
    send_data: function() {
      let crypto = require('crypto');
      let sha256 = crypto.createHash("sha256");
      sha256.update(this.password);
      let hash = sha256.digest("hex")
      let auth_data = {
          login: this.login,
          password: hash,
      };
      const user_data = JSON.stringify(auth_data, null, 2);
      this.$store.dispatch('login', {'user_data': user_data, 'login': this.login, 'remember': this.remember})
      .then(() => this.$router.push('/'))
      .catch(err => {
        this.wrong_text = err
        this.wrong_data = true
        })
    },
  }
}
</script>